<template>
  <div class="pay-order-wrap" :class="{'header-below': isPc}">
    <div :class="{'pay-order-pc': isPc,'pay-order-mobile': !isPc}">
      <div :class="{'introduce-container': isPc, 'introduce-container-mobile': !isPc}">
        <div class="introduce" v-if="!isPc">
          <h1>{{ $t('order/place:screen1.Title') }}</h1>
          <div>
            <p>{{ $t('order/place:screen1.Paragraph-line-1') }}</p>
            <p>{{ $t('order/place:screen1.Paragraph-line-2') }}</p>
            <p>{{ $t('order/place:screen1.Paragraph-line-3') }}</p>
            <p>{{ $t('order/place:screen1.Paragraph-line-4') }}</p>
            <p>{{ $t('order/place:screen1.Paragraph-line-5') }}</p>
            <p>{{ $t('order/place:screen1.Paragraph-line-6') }}</p>
          </div>
        </div>
        <div class="introduce-content" v-if="isPc">
          <h1>{{ $t('order/place:screen1.Title') }}</h1>
          <div class="detail">
            <div style="margin-right: 48px;">
              <p>{{ $t('order/place:screen1.Paragraph-line-1') }}</p>
              <p>{{ $t('order/place:screen1.Paragraph-line-2') }}</p>
              <p>{{ $t('order/place:screen1.Paragraph-line-3') }}</p>
            </div>
            <div>
              <p>{{ $t('order/place:screen1.Paragraph-line-4') }}</p>
              <p>{{ $t('order/place:screen1.Paragraph-line-5') }}</p>
              <p>{{ $t('order/place:screen1.Paragraph-line-6') }}</p>
            </div>
          </div>
        </div>
      </div>
      <section class="step step1">
        <div class="header">
          <div class="header-left">
            <span class="header-index">1</span>
            {{ $t('order/place:screen2:renewable.Title') }}
          </div>
          <div class="cycle-switch" v-if="isPc">
            <div class="cycle-container">
              <div class="cycle-content">
                <a class="cycle-item"
                   v-for="(item, i) in categoryList"
                   @click="onSelected(i)"
                   :key="i">
                  {{ item.name }}</a>
              </div>
              <a id="testtest" class="selected-bg" style="right:81%; width:88px"></a>
            </div>
          </div>
          <div class="coupon-box" :class="{'add-coupon':isAddCoupon && !isUseCoupon,'use-coupon':isAddCoupon && couponCode.length > 0 && !isUseCoupon,
          'use-coupon-success':useCouponSuccess && isUseCoupon,'use-coupon-failure':!useCouponSuccess && isUseCoupon}"
               v-if="isPc">
            <p class="p-add" v-if="!isAddCoupon || isUseCoupon">{{
                !isAddCoupon ?
                    $t('order/place:screen2.AddCode') :
                    isUseCoupon ? useCouponSuccess ? '已使用优惠券' : errorMsg : ''
              }}</p>
            <a-input :placeholder="$t('order/place:screen2.Text')" v-model="couponCode"
                     v-if="isAddCoupon && !isUseCoupon"></a-input>
            <a class="i-use-coupon" v-if="isAddCoupon && couponCode.length > 0 && !isUseCoupon"
               @click="useCoupon"></a>
            <a class="i-add-coupon" v-if="isAddCoupon || !isAddCoupon || isUseCoupon"
               @click="addCoupon()"></a>
          </div>
        </div>
        <PcProducts
            @selectedProduct="updateProduct"
            v-bind:selectedProduct="selectedProduct"
            v-bind:productList="productsList"></PcProducts>
        <div class="coupon-container">
          <div class="coupon-box" :class="{'add-coupon':isAddCoupon && !isUseCoupon,'use-coupon':isAddCoupon && couponCode.length > 0 && !isUseCoupon,
          'use-coupon-success':useCouponSuccess && isUseCoupon,'use-coupon-failure':!useCouponSuccess && isUseCoupon}"
               v-if="!isPc">
            <p class="p-add" v-if="!isAddCoupon || isUseCoupon" @click="addCoupon()">{{
                !isAddCoupon ?
                    $t('order/place:screen2.AddCode') :
                    isUseCoupon ? useCouponSuccess ? '已使用优惠券' : errorMsg : ''
              }}</p>
            <a-input :placeholder="$t('order/place:screen2.Text')" v-model="couponCode"
                     v-if="isAddCoupon && !isUseCoupon"></a-input>
            <a class="i-use-coupon" v-if="isAddCoupon && couponCode.length > 0 && !isUseCoupon"
               @click="useCoupon"></a>
            <a class="i-add-coupon" v-if="isAddCoupon || !isAddCoupon || isUseCoupon"
               @click="addCoupon()"></a>
          </div>
        </div>
      </section>
      <div class="email-section">
        <section class="step2">
          <div class="header">
            <span class="header-index">2</span>
            {{ $t('order/place:screen3:renewable.Title') }}
          </div>
          <div class="email-section-content">
            <div :class="{'create-account-box': isPc,'create-account-box-mobile': !isPc}">
              <div class="email-client"
                   :class="{'chosen-email-client': isEmailClient, 'email-client-bg': !isEmailClient}"
                   :style="{'z-index': isEmailClient? 1 :0}">
                <div class="client-header">
                  <span class="header-title" :class="{'header-selected': isEmailClient}">{{
                      $t('order/place:screen3:renewable.Text-1')
                    }}</span>
                  <span class="header-tip">{{ $t('order/place:screen3:renewable.Text-2') }}</span>
                </div>
                <div class="client-info" v-show="isEmailClient">
                  <a-input class="email-input" :placeholder="$t('Login.Text-3')" v-model="email"
                           @focusout="emailFocusout()"
                           @focusin="emailFocusin()" :disabled="isLogin"
                           @change="emailChange()"
                           @pressEnter="emailFocusout()"/>
                  <a-input class="email-input" :placeholder="$t('Login.Text-4')" v-model="password" v-if="!isLogin"/>
                  <div class="tips-container">
                    <span class="tips-msg" v-if="showTips" :class="{'error-msg': isErrorMsg}">{{ tipsMsg }}</span>
                    <ul class="all-tips">
                      <li class="input_tips">{{ $t('order/place:screen3:renewable.Text-3') }}</li>
                      <li class="input_tips">{{ $t('order/place:screen3:renewable.Text-4') }}</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <section class="step step3">
        <div class="header">
          <span class="header-index">3</span>
          {{ $t('order/place:screen4.Title') }}
        </div>
        <div>
          <div class="pay-content" v-if="isPc">
            <div class="pay-method">
              <div class="method" :class="{'method-selected': selectedMethod === 1}" @click="changeMethod(payment.alipay, 1)"
                   v-if="payment.alipay">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 1}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="alipay"></i>
                    </div>
                    <p class="method-name">支付宝</p>
                  </div>
                </div>
              </div>
              <div class="method" :class="{'method-selected': selectedMethod === 3}" @click="changeMethod(payment.wechat, 3)"
                   v-if="payment.wechat">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 3}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="wechatpay"></i>
                    </div>
                    <p class="method-name">微信支付</p>
                  </div>
                </div>
              </div>
              <div class="method" :class="{'method-selected': selectedMethod === 4}" @click="changeMethod(payment.crypto, 4)"
                   v-if="payment.crypto">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 4}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="btc"></i>
                    </div>
                    <p class="method-name">数字货币</p>
                  </div>
                </div>
              </div>
              <div class="method" :class="{'method-selected': selectedMethod === 0}" @click="changeMethod('credit', 0)"
                   v-if="isLogin">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 0}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="wallet"></i>
                    </div>
                    <p class="method-name">余额支付</p>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <div class="popover-arrow"></div>
              <div class="package-list">
                <div class="list-item">
                  <p>{{ $t('order/place:screen4.List1') }}</p>
                  <p>{{ selectedProduct.category ? selectedProduct.category.name : '' }} - {{
                      selectedProduct.name
                    }}</p>
                </div>
                <div class="list-item">
                  <p>{{ $t('order/place:screen4.List2') }}</p>
                  <div class="sub-cycle-list-box">
                    <div class="cycle-title-box" :class="{'show-cursor': selectBillingCycleList.length > 0}">
                      <p>{{ selectedProduct.days }} 天</p>
                      <i class="down-icon"
                         v-if="!selectedProduct['useTryPlan'] && selectBillingCycleList.length > 0"></i>
                    </div>
                  </div>
                </div>
                <div class="list-item">
                  <p>{{ $t('order/place:screen4.List3') }} </p>
                  <p>{{
                      selectedProduct.payPreferentialPrice ? selectedProduct.payPreferentialPrice : selectedProduct.price
                    }}
                    元</p>
                </div>
                <div class="list-item">
                  <p>{{ $t('order/place:screen4.List5') }}</p>
                  <p class="reduce">- {{ selectedProduct.couponPrice ? selectedProduct.couponPrice : 0 }} 元</p>
                </div>
                <div class="list-item">
                  <p>{{ $t('order/place:screen4.List7') }}</p>
                  <a class="auto-renew-uncheck" :class="{'auto-renew-checked' : isAutoRenew}"
                     @click="isAutoRenew = !isAutoRenew"></a>
                </div>
                <div class="pay-line"></div>
                <div class="total">
                  <p>{{ $t('order/place:screen4.List6') }}</p>
                  <p>{{ selectedProduct.price }} 元</p>
                </div>
                <a-button class="submit-btn" type="primary"
                          :class="{'submit-btn-loading': iconLoading, 'disable-order': !canOrder}"
                          icon="poweroff"
                          :loading="iconLoading"
                          @click="submitOrder">
                  {{ $t('order/place:screen4.Button') }}
                </a-button>
                <span class="error-tip"
                      :class="{ 'error': !errorDisplay && !canLogin, 'login-tips': canLogin }">{{
                    errorDescription
                  }}<a @click="$router.push({path: '/user/service/invoice'})"
                       v-if="showGoPay"> 去支付 </a><span>。</span></span>
                <div class="foot-tips">
                  <ul>
                    <li>{{ $t('order/place:screen4.Tips-1-1') }}
                      <span>{{ $t('order/place:screen4.Tips-1-2') }}</span>
                    </li>
                    <li>
                      {{
                        $t(cycleType !== 'onetime' ? 'order/place:screen4.Tips-2-1-1' : 'order/place:screen4.Tips-2-1-2')
                      }}
                      <span>{{ $t('order/place:screen4.Tips-2-2') }}</span>
                    </li>
                    <li style="word-wrap:break-word;word-break:break-all;">
                      {{ $t('order/place:screen4.Tips-3') }}
                    </li>
                  </ul>

                </div>
              </div>
            </div>
            <div class="safe-tips">
              <div class="safe-logo">
                <i class="norton"></i>
                <i v-if="this.$i18n.locale !== 'en-US' " class="thirty-day-refund"></i>
                <i v-if="this.$i18n.locale === 'en-US'" class="thirty-day-refund-us"></i>
              </div>
              <p>{{ $t('order/place:screen4.Features') }}</p>
              <div class="features">
                <p>{{ $t('order/place:screen4.Paragraph-line-1') }}</p>
                <p>{{
                    $t(cycleType !== 'onetime' ? 'order/place:screen4.Paragraph-line-2-1' :
                        'order/place:screen4.Paragraph-line-2-2')
                  }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-3') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-4') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-5') }}</p>
              </div>
            </div>
          </div>
          <div class="pay-content-mobile" v-if="!isPc">
            <div class="safe-logo">
              <i class="norton"></i>
              <i v-if="this.$i18n.locale !== 'en-US' " class="thirty-day-refund"></i>
              <i v-if="this.$i18n.locale === 'en-US'" class="thirty-day-refund-us"></i>
            </div>
            <div class="pay-method">
              <div class="method" :class="{'method-selected': selectedMethod === 0}" @click="selectedMethod = 0"
                   v-if="payment.alipay">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 0}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="alipay"></i>
                    </div>
                    <p class="method-name">支付宝</p>
                  </div>
                </div>
              </div>
              <div class="method" :class="{'method-selected': selectedMethod === 1}" @click="selectedMethod = 1"
                   v-if="payment.wechat">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 1}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="wechatpay"></i>
                    </div>
                    <p class="method-name">微信支付</p>
                  </div>
                </div>
              </div>
              <div class="method" :class="{'method-selected': selectedMethod === 2}" @click="selectedMethod = 2"
                   v-if="payment.crypto">
                <div class="choose">
                  <div class="pay-radio" :class="{'light': selectedMethod === 2}">
                    <i></i>
                  </div>
                  <div class="pay-detail">
                    <div class="pay-logo">
                      <i class="btc"></i>
                    </div>
                    <p class="method-name">数字货币</p>
                  </div>
                </div>
              </div>
              <div class="method">
                <div class="pay-info">
                  <div class="package-list">
                    <div class="list-item">
                      <p>{{ $t('order/place:screen4.List1') }}</p>
                      <p>{{ $t(selectedProduct.name) }}</p>
                    </div>
                    <div class="list-item">
                      <p>{{ $t('order/place:screen4.List2') }}</p>
                      <div class="sub-cycle-list-box">
                        <div class="cycle-title-box"
                             :class="{'show-cursor': selectBillingCycleList.length > 0}">
                          <p>{{
                              $t(selectedProduct.billingCycle === 'monthly' ?
                                  'order/place:screen2:tab1.Tab' : selectedProduct.billingCycle === 'semiannually' ?
                                      'order/place:screen2:tab3.Tab' : 'order/place:screen2:tab4.Tab')
                            }}<span>{{ selectedProduct['useTryPlan'] ? ' - 先試用7天' : '' }}</span></p>
                          <i class="down-icon"
                             v-if="!selectedProduct['useTryPlan'] && selectBillingCycleList.length > 0"></i>
                        </div>
                        <div class="cycle-list-box"
                             v-if="!selectedProduct['useTryPlan'] && selectBillingCycleList.length > 0">
                          <div class="popover-arrow" :class="{'popover-arrow-bg-change': false}"></div>
                          <div class="cycle-content-box">
                            <p v-for="(item,index) in selectBillingCycleList" :key="index"
                               @click="switchCycle(item)">
                              {{ $t(item.name) }}</p>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="list-item">
                      <p>{{ $t('order/place:screen4.List3') }} </p>
                      <p>{{
                          selectedProduct.payPreferentialPrice ? selectedProduct.payPreferentialPrice : selectedProduct.price
                        }} 元</p>
                    </div>
                    <div class="list-item">
                      <p>{{ $t('order/place:screen4.List5') }}</p>
                      <p class="reduce">- {{ !selectedProduct.couponPrice ? 0 : selectedProduct.couponPrice }}
                        元</p>
                    </div>
                    <div class="list-item">
                      <p>{{ '开启自动续费' }}</p>
                      <a class="auto-renew-uncheck" :class="{'auto-renew-checked' : isAutoRenew}"
                         @click="isAutoRenew = !isAutoRenew"></a>
                    </div>
                    <div class="total">
                      <p>{{ $t('order/place:screen4.List6') }}</p>
                      <p>{{ selectedProduct.price }} 元</p>
                    </div>
                  </div>
                  <div class="footer">
                    <p>承诺商品可用，请放心购买。 </p>
                    <!--     <p>承诺30天可 <span @click="goToPolicyDetail()">退款</span>，请放心购买。 </p>-->
                  </div>
                </div>
              </div>
            </div>
            <div class="submit" id="submit">
              <div class="protocol">
                <!--<div class="checkbox" @click="agreeProtocol = !agreeProtocol"
                     :class="{'light': agreeProtocol}">
                  <i></i>
                </div>-->
                <p>{{ $t('order/place:screen4.Tips-1-1') }}</p>
                <span @click="goToTermsService">{{ $t('order/place:screen4.Tips-1-2') }}</span>
              </div>
              <a-button class="submit-btn" type="primary"
                        :class="{'submit-btn-loading': iconLoading, 'disable-order': !canOrder}" icon="poweroff"
                        :loading="iconLoading"
                        @click="submitOrder">
                {{ $t('order/place:screen4.Button') }}
              </a-button>
              <span id="bottomErrorTip" class="error-tip"
                    :class="{ 'error': !errorDisplay && !canLogin, 'login-tips': canLogin  }">{{ errorDescription }}<a
                  v-if="canLogin" @click="gotToLogin()"> 登入 </a><span v-if="canLogin">。</span></span>
            </div>
            <div class="bottom-features">
              <p>{{ $t('order/place:screen4.Features') }}</p>
              <div class="features">
                <p>{{ $t('order/place:screen4.Paragraph-line-1') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-2-1') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-3') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-4') }}</p>
                <p>{{ $t('order/place:screen4.Paragraph-line-5') }}</p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import PcProducts from '../components/PcProducts';
import {AU_HMAC, AU_EMAIL, AU_AFF, AU_UUID, AU_IN_APP} from "@/store/Global";

export default {
  name: "shop",
  components: {
    PcProducts
  },
  data() {
    return {
      isPc: this.isPc(),
      categoryList: [],
      productsList: [],
      allProductsList: [],
      isEmailClient: true,
      isAddCoupon: false,
      couponCode: '',
      tipsMsg: this.$t('Login.Text-6'),
      isLogin: localStorage.getItem(AU_HMAC) ? true : false,
      isUseCoupon: false,
      useCouponSuccess: false,
      selectedProduct: {},
      selectedIndex: 0,
      selectedMethod: 1,
      email: localStorage.getItem(AU_EMAIL),
      password: '',
      canOrder: false,
      showTips: false,
      isErrorMsg: false,
      payment: {},
      selectBillingCycleList: [],
      isAutoRenew: false,
      iconLoading: false,
      errorDisplay: false,
      canLogin: false,
      errorDescription: this.$t('order/place:screen4.Warning-1'),
      showGoPay: false,
      cycleType: 'monthly',
      pay_type: ''
    }
  },
  mounted() {
    this.getProducts();
  },
  methods: {
    getProducts() {
      let that = this;
      if (that.isLogin) {
        that.canOrder = true;
      }
      that.request.getProducts().then((res) => {
        if (res.code === 1) {
          that.payment = res.data.payment;
          that.pay_type = res.data.payment.alipay
          that.categoryList = res.data.category;
          that.allProductsList = res.data.list;
          that.selectedIndex = 0;
          setTimeout(() => {
            that.updateUi();
          }, 100)
          that.productsList = that.allProductsList[that.categoryList[0]['name']];
          that.productsList.forEach(function (item, index) {
            that.productsList[index]['billingCycle'] = index;
          })
          that.selectedProduct = Object.assign({}, that.productsList[0], {});
        }
      });
    },
    changeMethod(method, type){
       this.selectedMethod = type;
       this.pay_type = method;
       console.log(method)
    },
    updateProduct(selectedP) {
      this.selectedProduct = Object.assign({}, selectedP, {});
    },
    onSelected(e) {
      let that = this;
      that.productsList = that.allProductsList[that.categoryList[e]['name']];
      that.productsList.forEach(function (item, index) {
        that.productsList[index]['billingCycle'] = index;
      })
      that.selectedProduct = Object.assign({}, that.productsList[0], {});
      that.selectedIndex = e;
      that.updateUi()
    },
    updateUi() {
      let e = 0;
      let t = document.getElementsByClassName("cycle-item")[this.selectedIndex].offsetLeft;
      let a = document.getElementsByClassName("cycle-item")[this.selectedIndex].clientWidth;
      let i = document.getElementsByClassName("cycle-content")[0].clientWidth;
      e = i - t - a + 2;
      document.getElementsByClassName("selected-bg").item(0).setAttribute("style", "right:" + e + "px; width:" + document.getElementsByClassName("cycle-item")[this.selectedIndex].clientWidth + "px")
    },
    submitOrder() {
      if (!this.canOrder) {
        this.errorDisplay = true;
        this.errorDescription = '请输入邮箱和密码！';
        return;
      }
      if(!this.isLogin && !this.password){
        this.errorDisplay = true;
        this.errorDescription = '请输入密码！';
        return;
      }
      this.errorDisplay = false;
      let param = {
        "shop_id": this.selectedProduct.id,
      };
      param["email"] = this.email;
      param["password"] = this.password;
      param['method'] = this.pay_type;
      param['pay_type'] = this.selectedMethod;
      // 优惠券
      if (this.selectedProduct.promocode) {
        param["couponCode"] = this.selectedProduct.promocode;
      }
      // add aff
      if (localStorage.getItem(AU_AFF)) {
        param["aff"] = localStorage.getItem(AU_AFF)
      }
      this.iconLoading = true;
      const that = this;
      // get the device and traffic
      param['device'] = this.getDevice();
      that.request.submitOrder(param).then(res => {
        that.iconLoading = false;
        if (res.code === 1) {
          let data2 = res.data;
          localStorage.setItem(AU_UUID, data2.user.uuid);
          if (data2.user.hmac) {
            localStorage.setItem(AU_HMAC, data2.user.hmac);
          }
          localStorage.setItem(AU_EMAIL, data2.user.email);
          // send gtag
          let gtagData = {
            coupon: param.promocode ? param.promocode : '',
            currency: '￥',
            value: that.selectedProduct['useTryPlan'] ? '0.9' : that.selectedProduct.payPreferentialPrice,
            traffic_type: process.env.NODE_ENV === 'production' ? 'normal' : 'internal',
            items: [{
              item_id: param.selectedProduct,
              item_name: that.selectedProduct.name,
              promotion_id: 0,
              promotion_name: 'none',
              affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
              coupon: param.promocode ? param.promocode : '',
              item_variant: param.billingcycle,
              price: that.selectedProduct['useTryPlan'] ? '0.9' : that.selectedProduct.payPreferentialPrice,
              currency: '￥'
            }]
          }

          that.$router.push({
            path: 'order/pay',
            name: 'payment',
            params: {
              gtagData: gtagData
            },
            query: {
              invoiceId: data2.id,
            }
          });
        } else {
          if (data.data.msg) {
            that.errorDescription = data.data.msg;
            that.errorDisplay = true;
            if (data.data.msg == '订单生成错误：尚有未支付的订单，请先去支付') {
              that.showGoPay = true;
            }
          } else {
            that.errorDescription = that.$t('order/place:screen4.Warning-2');
            that.errorDisplay = true;
          }
        }
      })
    },
    useCoupon() {
      const that = this;
      that.request.checkCoupon({
        coupon_sn: that.couponCode,
        price: that.selectedProduct.price
      }).then(res => {
        that.isUseCoupon = true;
        if (res.code === 1) {
          let coupon = res.data;
          that.useCouponSuccess = true;
          if (coupon.type === 1) {
            // 按金额
            that.productsList.forEach(item => {
              item.couponPrice = coupon.value;
              item.payPreferentialPrice = item.price;
              item.promocode = that.couponCode;
              item.price = (item.price - coupon.value).toFixed(2);
            });
            that.selectedProduct.couponPrice = coupon.value;
            that.selectedProduct.payPreferentialPrice = item.price;
            that.selectedProduct.promocode = that.couponCode;
            that.selectedProduct.price = (item.price - coupon.value).toFixed(2);
          } else {
            // 按比例
            let credit = parseFloat(coupon.value / 100);
            that.productsList.forEach(item => {
              item.couponPrice = that.mathRound(item.price - (item.price * credit));
              item.payPreferentialPrice = item.price;
              item.promocode = that.couponCode;
              item.price = (item.price - item.couponPrice).toFixed(2);
            });
            that.selectedProduct.couponPrice = that.mathRound(item.price - (item.price * credit));
            that.selectedProduct.payPreferentialPrice = item.price;
            that.selectedProduct.promocode = that.couponCode;
            that.selectedProduct.price = (item.price - item.couponPrice).toFixed(2);
          }
          that.compareSavePercent();
        } else {
          that.useCouponSuccess = false;
          that.errorMsg = res.msg;
        }
      });
    },
    addCoupon() {
      this.isAddCoupon = !this.isAddCoupon;
      if (!this.isAddCoupon) {
        this.isUseCoupon = false;
        this.couponCode = '';
        this.productsList.forEach(item => {
          if (item.isCanUseCoupon) {
            item.isCanUseCoupon = false;
            item.couponPrice = 0;
            item.payPreferentialPrice = this.mathRound(item.payOriginalPrice - item.payReduce);
            item.price = this.mathRound((item.payOriginalPrice - item.payPreferentialPrice) / item.payOriginalPrice * 100);
          }
        });
        this.updateCycle(this.selectedProduct.billingCycle);
      }
    },
    mathRound(num) {
      return Math.round(num * 100) / 100;
    },
    updateCycle(cycle) {
      this.selectedProduct = Object.assign({}, this.productsList[0], {});
    },
    emailFocusout() {
      let that = this
      if (that.email.length <= 0) {
        return;
      }
      if (!that.emailValidate(that.email)) {
        that.showTips = true
        that.isErrorMsg = true
        that.canOrder = false
        return
      }
      this.request.checkEmail({email: that.email}).then(res => {
         if(res.code === 1){
           that.canOrder = true
         } else {
           that.showTips = true;
           that.isErrorMsg = true;
           that.canOrder = false;
           that.tipsMsg = res.msg;
         }
      });
    },
    emailValidate(email) {
      const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
      return emailReg.test(email);
    },
    emailFocusin() {
      this.isErrorMsg = false
      this.showTips = false
      this.canLogin = false
    },
    emailChange() {
      if (this.email.length <= 0 && this.password.length < 1) {
        return
      }
      if (!this.emailValidate(this.email)) {
        this.showTips = true
        this.isErrorMsg = true
        this.canOrder = false
        return
      }
    },
    getDevice() {
      let uA = navigator.userAgent.toLowerCase();
      let ipad = uA.match(/ipad/i) !== null;
      let iphone = uA.match(/iphone/i) !== null;
      let android = uA.match(/android/i) !== null;
      let windows = uA.match(/windows/i) !== null;
      let mac = uA.match(/mac os/i) !== null;
      let inApp = localStorage.getItem(AU_IN_APP)
      if (inApp) {
        return 'android-app'
      } else {
        return ipad || iphone ? 'ios-browser' : android ? 'android-browser' : windows ? 'pc-browser' : mac ? 'mac-browser' : 'unknown'
      }
    },
    goToTermsService() {
      this.articleId = Object.assign(360036448091);
      //window.open(`${VUE_SITE}/help/article/360036448091`,'_blank');
    },
    goToPolicyDetail() {
      this.articleId = Object.assign(360004852651);
      //window.open(`${VUE_SITE}/help/article/360004852651`,'_blank');
    },
  }
}
</script>

