.create-account-box {
  width: 450px;
  height: 338px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 2px 6px rgba(234, 238, 246, 0.6);

  div {
    display: flex;
    flex-direction: column;
  }

  .header-selected {
    &::after {
      display: block;
      width: 18px;
      height: 18px;
      margin-left: 8px;
      background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-plan-checked.svg') center no-repeat;
      background-size: contain;
      cursor: pointer;
      content: '';
    }
  }

  .header-title {
    font-size: 18px;
    font-weight: 500;
    color: #616577;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .header-tip {
    border-radius: 4px;
    background-color: #F5F6F8;
    padding: 6px 16px;
    font-size: 14px;
    font-weight: 400;
  }

  .client-header {
    height: 32px;
    flex-direction: row;
    justify-content: space-between;
  }

  .client-info {
    margin-top: 16px;
  }

  .all-tips {
    padding-left: 15px;
    margin-bottom: 0;

    .input_tips {
      margin-top: 8px;
      color: #9DA3B9;
      font-size: 14px;

    }
  }


  .email-client {
    padding: 32px;
    border-radius: 4px;
    border: 1px solid #E4EAF0;
    position: absolute;
    top: 0;
    background-color: #fff;
    width: 100%;
    height: 249px;
    cursor: pointer;

    .client-info {
      .email-input {
        width: 386px;
        height: 48px;
        margin-bottom: 12px;
      }

      .tips-container {
        max-height: 84px;

        .tips-msg {
          font-size: 0.06rem;
          font-weight: 400;
          color: #616577;
          display: flex;
          align-items: center;

          &::before {
            content: "";
            //background: url('../assets/suppor/tic-bind-email-success.svg') center no-repeat;
            background-size: contain;
            display: inline-block;
            width: 14px;
            height: 14px;
            margin-right: 8px;
          }
        }

        .error-msg {
          color: #FC5D60;

          &::before {
            content: "";
            //background: url('../assets/suppor/tic-bind-email-error.svg') center no-repeat;
            background-size: contain;
            display: inline-block;
            width: 14px;
            height: 14px;
            margin-right: 8px;
          }
        }
      }
    }
  }

  .chosen-email-client {
    height: 100%;
    border-left: 3px solid #3963EF;
    box-shadow: 0 2px 6px rgba(234, 238, 246, 0.4);

    .header-title {
      color: #3963EF;
    }
  }

  .email-client-bg {
    &:hover {
      background-color: #F8FAFC;
    }
  }

  .anonymous-client {
    padding: 29px 32px;
    border-radius: 4px;
    border: 1px solid #E4EAF0;
    z-index: 0;
    position: absolute;
    bottom: 0;
    background-color: #fff;
    width: 100%;

    .client-info {
      .btn-container {
        width: 386px;
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #DEE2E8;
        border-radius: 4px;
        margin-bottom: 12px;

        .use-btn {
          background-color: #3963EF;
          width: fit-content;
          padding: 11px 24px;
          height: fit-content;
          font-size: 16px;
          font-weight: 400;
        }
      }

      .addr-container {
        border: 1px solid #DEE2E8;
        border-radius: 4px;
        position: relative;
        flex-direction: column;
        background-color: #F9FAFB;
        height: 100px;

        .user-address {
          padding: 16px 20px;
          width: 100%;
          height: 50%;
          color: #9DA3B9;
          font-size: 14px;
          font-weight: 400;
          word-break: keep-all;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .copy-tips {
          &:hover {
            opacity: 1;
          }

          cursor: pointer;
          position: absolute;
          width: 100%;
          height: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 1;
          background-color: rgba(255, 255, 255, 0.7);
          color: #3963EF;
          font-size: 16px;
          opacity: 0;
        }

        .addr-footer {
          flex-direction: row;
          width: 100%;
          height: 50%;
          background-color: #fff;
          border-bottom-left-radius: 4px;
          border-bottom-right-radius: 4px;
          color: #3963EF;
          border-top: 1px solid #DEE2E8;

          span {
            display: flex;
            width: 50%;
            justify-content: center;
            align-items: center;
            cursor: pointer;
          }

          .download-span {
            border-right: 1px solid #DEE2E8;

            &::before {
              width: 20px;
              height: 20px;
              margin-right: 4px;
              background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-download.svg') center no-repeat;
              background-size: contain;
              content: '';
            }
          }

          .drag-bg {
            position: absolute;
            opacity: 0.1;
            top: 50px;
            left: 190px;
            width: 190px;
            height: 48px;
          }

          .favorite-span {
            &::before {
              width: 20px;
              height: 20px;
              margin-right: 4px;
              background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-favorite.svg') center no-repeat;
              background-size: contain;
              content: '';
            }
          }
        }
      }

      .all-tips {
        margin-bottom: 0;

        .input_tips1 {
          margin-top: 0px;
          color: #9DA3B9;
          font-size: 14px;
        }

        .input_tips1:last-child {
          margin-bottom: -13px;
        }

        .input_tips {
          margin-top: 20px;
        }
      }
    }
  }

  .chosen-anonymous-client {
    display: flex;
    justify-content: flex-end;
    box-shadow: 0 -2px 6px rgba(234, 238, 246, 0.4);
    border-left: 3px solid #3963EF;

    .header-title {
      color: #3963EF;
    }
  }

  .anonymous-client-bg {
    &:hover {
      background-color: #F8FAFC;
    }
  }
}

