.pay-content {
  display: flex;
  flex-direction: row;
  margin-top: 36px;

  .pay-method {
    height: fit-content;
    width: 232px;
    background: #fff;
    border-radius: 4px;
    border: 1px solid #E6EBF0;
    box-sizing: border-box;

    .method {
      height: 110px;
      width: 230px;
      background-color: #fff;

      &:first-child {
        .choose {
          border-top: none;

          p {
            margin-top: 0;
          }
        }
      }

      .choose {
        display: flex;
        flex-direction: row;
        border-top: 1px solid #E7E9ED;

        .pay-radio {
          margin: 24px 0 0 24px;
          display: flex;
          width: 28px;
          height: 28px;
          align-items: center;
          justify-content: center;
          border-radius: 18px;
          border: 2px solid #E6EBF0;
          box-sizing: border-box;

          i {
            width: 24px;
          }

          &.light {
            background: #138CFF;
            border-color: #138CFF;

            i {
              display: inline-block;
              content: '';
              height: 12px;
              background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/hook-white.svg') center no-repeat;
              background-size: contain;
            }
          }
        }

        .pay-detail {
          margin: 28px 0 0 20px;

          .pay-logo {
            text-align: center;

            i {
              display: inline-block;
              height: 20px;
              margin-right: 6px;
              background: no-repeat center;
              background-size: contain;

              &.alipay {
                width: 58px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/alipay.svg') center no-repeat;
              }

              &.wechatpay {
                width: 64px;
                background: url('../assets/support/wechat.svg') center no-repeat;
              }

              &.paypal {
                width: 78px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/paypal.svg') center no-repeat;
              }

              &.ic-unionpay {
                width: 32px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-unionpay-new.svg') center no-repeat;
              }

              &.unionpay {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/union-pay.svg') center no-repeat;
              }

              &.master {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/master.svg') center no-repeat;
              }

              &.amex {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/amex.svg') center no-repeat;
              }

              &.visa {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/visa-pay.svg') center no-repeat;
              }

              &.btc {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/btc.svg') center no-repeat;
              }

              &.btc2 {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/btc2.svg') center no-repeat;
              }

              &.eth {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/eth.svg') center no-repeat;
              }

              &.eth2 {
                width: 28px;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/eth2.svg') center no-repeat;
              }

              &.wallet {
                width: 28px;
                height: 26px;
                background: url('../assets/support/wallet.svg') center no-repeat;
              }
            }
          }

          .method-name {
            margin: 12px 0 0 0;
            color: #383E44;
            font-size: 16px;
            line-height: 22px;
          }
        }

      }

      &:hover {
        background-color: #E7F3FF;
      }
    }

    .method-selected {
      background-color: #E7F3FF;
    }
  }

  .package-list {
    position: relative;
    top: -24px;
    border-radius: 4px;
    width: 510px;
    margin-left: 47px;
    padding: 40px 40px 26px 40px;
    border: 1px solid #E7E9ED;
    background-color: #fff;

    .list-item {
      display: flex;
      justify-content: space-between;
      font-size: 14px;

      p {
        color: #768392;
        margin-bottom: 16px;

        &.reduce {
          color: #3EC68D;
        }

        .reduce-tips {
          color: #138CFF;
        }
      }

      .auto-renew-uncheck {
        display: inline-block;
        height: 18px;
        width: 18px;
        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-auto-uncheck.svg') center no-repeat;
        background-size: contain;
      }

      .auto-renew-checked {
        background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-auto-checked.svg') center no-repeat;
      }

      .radio-group {
        .ant-radio-wrapper:last-child {
          margin-right: 0;

          span:last-child {
            padding-right: 0;
          }
