<template>
    <div :class="{'package-container': isPc,'package-container-mobile': !isPc}">
        <a-spin class="product-detail-loading" v-if="packageList.length === 0">
            <a-icon slot="indicator" type="loading" style="font-size: 70px" spin/>
        </a-spin>
        <div class="package-wrap" v-for="(item, index) in packageList" :key="index"
             @click="stepOneClick(index)">
            <div class="package-content" :class="{'select-package': selectedProductIndex === index}"
                 :style="{margin: index === 1 && isPc ? '0px 24px' : '0px'}">
                <div class="save-percent">
                    <p class="normal big-show"  v-if="item.limit_num > 0">
                        {{ item.limit_num > 0 ? $t(cardInfo['Save-1']) + ' ' + item.limit_num + ' 次' : '' }}
                    </p>
                </div>
                <a class="radio" :class="{'light': selectedProductIndex === index}" @click="stepOneClick(index)"></a>
                <a-tooltip placement="bottomRight" arrowPointAtCenter>
                    <template slot="title">
                        <p>
                            {{
                                $t(index === 0 ? cardInfo['QuestionMark-1-1'] : index === 1 ? cardInfo['QuestionMark-2-1'] : cardInfo['QuestionMark-3-1'])
                            }}</p>
                        <p>
                            {{
                                $t(index === 0 ? cardInfo['QuestionMark-1-2'] : index === 1 ? cardInfo['QuestionMark-2-2'] : cardInfo['QuestionMark-3-2'])
                            }}</p>
                        <p>
                            {{
                                $t(index === 0 ? cardInfo['QuestionMark-1-3'] : index === 1 ? cardInfo['QuestionMark-2-3'] : cardInfo['QuestionMark-3-2'])
                            }}</p>
                        <p>
                            {{
                                $t(index === 0 ? cardInfo['QuestionMark-1-4'] : index === 1 ? cardInfo['QuestionMark-1-4'] : cardInfo['QuestionMark-1-4'])
                            }}</p>
                    </template>
                    <a class="question"></a>
                </a-tooltip>
                <div class="package-info" @click="stepOneClick(index)">
                    <div class="title">
                        {{ item.name }}
                    </div>
                    <p class="description">
                        {{ item.description }}
                    </p>
                    <div class="price-content">
                        <span class="price">{{ item.price }}</span>
                        <div class="right">
                            <div class="coupon-box" :class="{'length-more-than-three': false}">
                                <div class="coupon-none" :class="{'coupon-label': item.is_hot == 1}">
                                    <span>{{
                                            $t(item.is_hot === 1 ? cardInfo['Discount-2'] : cardInfo['Discount-3'])
                                        }}</span>
                                </div>
                            </div>
                            <span>/ {{ item.days }} 天</span>
                        </div>
                    </div>
                </div>
                <div class="recommand-wrap" @click="stepOneClick(index)">
                    <div class="price">
                        <div class="after">
                           流量：{{ item.traffic_label }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/store/Global";

export default {
    name: 'PcProducts',
    components: {},
    data() {
        return {
            selectedProductIndex: 0,
            packageList: [],
            cardInfo: {},
            hideTryPlan: false,
            isPc: this.isPc(),
        }
    },
    props: {
        productList: {},
        selectedProduct: {},
    },
    watch: {
        productList: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.packageList = value;
                }
            }
        },
        selectedProduct: {
            immediate: true,    // 这句重要
            handler(val) {
                if (val != undefined) {
                    this.selectedProductIndex = val.billingCycle;
                }
            }
        }
    },
    created() {
        this.cardInfo = this.$t('order/place:screen2:tab1');
    },
    methods: {
        clickTipsBox(item, index) {
            item.useTryPlan = !item.useTryPlan;
            this.stepOneClick(index);
        },
        stepOneClick(e) {
            this.selectedProductIndex = e;
            this.packageList.forEach((item, index) => {
                if (index !== e) {
                    item.useTryPlan = false;
                }
            });
            this.$emit('selectedProduct', this.packageList[e]);
        },
    }
};
</script>

